import React from 'react'
import { useApp } from '../../context/Context';
import textContext from '../../textContext';
import { howImage } from '../../assets/images';

// Comps
import HowText from './HowText'

export default function How() {

	const { language } = useApp();
  const text = textContext[language].how;

	return (
		<div id="how">
			<div className="padding textContainer">
				<p className="textHeader">{text.title}</p>
				<div className="howContainer">
					<img src={howImage} alt="how it works" />
					<div>
						{
							text.list.map((l,i) => (
								<HowText key={i} l={l} />
							))
						}
					</div>
				</div>
			</div>
		</div>
	)
}